:host {
  @apply block h-full;

  ::ng-deep {
    .p-accordion {
      .p-accordion-header-link {
        @apply font-medium;

        &:focus {
          @apply shadow-none;
        }
      }

      .p-accordion-header-text {
        @apply font-semibold;
      }

      .p-accordion-content {
        @apply py-4 px-0;
      }
    }

    .p-divider {
      @apply my-4;
    }

    .section-container {
      @apply py-2;
    }
  }
}

.section-title {
  @apply text-lg font-medium mb-3 pb-1 border-b border-surface-200;
}
